  private buildUrl(
    endpoint: string,
    params?: Record<string, string | number>
  ): string {
    // ุงุณุชุฎุฏุงู ุงููุณูุท ุงููุญูู ุจุฏูุงู ูู ุงูุงุชุตุงู ุงููุจุงุดุฑ ุจู API
    // ุณูุชู ุชูุฌูู ุงูุทูุจุงุช ูู /api ุฅูู ุงูุฎุงุฏู ุงููุนูู ุนุจุฑ next.config.js
    let base = "/api";

    let path = endpoint;
    // ุฅุฒุงูุฉ /cretechsoft/Whatsapp ูู ุงููุณุงุฑ ุฅุฐุง ูุงู ููุฌูุฏุงู
    // ูุฃูู ูุฏ ูููู ูุถููุงู ุจุงููุนู ูู baseUrl
    if (path.startsWith("/cretechsoft/Whatsapp/")) {
      path = path.replace("/cretechsoft/Whatsapp/", "/");
    }
    if (path.startsWith("/")) {
      path = path.substring(1);
    }

    // ุจูุงุก URL ูุงูู
    const fullUrl = `${base}/${path}`;
    console.log(`๐ Built URL: ${fullUrl}`);

    // ุฅุถุงูุฉ ูุนููุงุช ุงูุงุณุชุนูุงู ุฅุฐุง ูุฌุฏุช
    if (!params || Object.keys(params).length === 0) {
      return fullUrl;
    }

    // ุจูุงุก ุณูุณูุฉ ูุนููุงุช ุงูุงุณุชุนูุงู
    const queryParams = new URLSearchParams();
    Object.entries(params).forEach(([key, value]) => {
      if (value !== undefined && value !== null) {
        queryParams.append(key, String(value));
      }
    });

    const queryString = queryParams.toString();
    if (queryString) {
      return `${fullUrl}?${queryString}`;
    }

    return fullUrl;
  }

  // ุงุณุชุฎุฏุงู ApiResponse<T> ุงูุฐู ููุช ุจุชุนุฑููู
  private async request<T>(
    endpoint: string,
    config: RequestConfig = {}
  ): Promise<ApiResponse<T>> {
    // ุชู ุชุญุฏูุซ ููุน ุงูุฅุฑุฌุงุน ููุง
    const { params, ...requestConfig } = config;

    const url = this.buildUrl(endpoint, params);

    // ุทุจุงุนุฉ ูุนูููุงุช ุชุดุฎูุตูุฉ
    console.log(`๐ API Request to: ${url}`);

    // ุชุญุณูู ุฑุคูุณ ุงูุทูุจ ูุชุชุถูู ุงููุฒูุฏ ูู ุงููุนูููุงุช ููุชูุซูู
    const defaultConfig: RequestInit = {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        // ุฅุฒุงูุฉ ุฑุคูุณ ุงูุชูุซูู ูู ุงูุทูุจ ุงููุจุฏุฆู (preflight)
        // ุณูุชู ุฅุถุงูุชูุง ุจูุงุณุทุฉ ุงูุฎุงุฏู ุงููุณูุท
        ...requestConfig.headers,
      },
      ...requestConfig,
      // ุชุนุฏูู ุฎูุงุฑุงุช ุงูุทูุจ ูุชุนูู ูุน ุงููุณูุท
      credentials: "same-origin", // ุชุบููุฑ ูู include ุฅูู same-origin ูุฃููุง ูุณุชุฎุฏู ูุณูุทุงู ูุญููุงู
      cache: "no-cache", // ุชุฌูุจ ูุดุงูู ุงูุชุฎุฒูู ุงููุคูุช
    };

    try {
      console.log(`๐ค Request Headers:`, defaultConfig.headers);
      console.log(`๐ค Request Method:`, defaultConfig.method || "GET");

      const response = await fetch(url, defaultConfig);
      console.log(`๐ฅ Response Status:`, response.status, response.statusText);

      if (!response.ok) {
        // ูุญุงููุฉ ุชุญููู ุฑุณุงูุฉ ุงูุฎุทุฃ ูู ุงูุงุณุชุฌุงุจุฉ
        let errorMessage = `HTTP Error ${response.status}: ${response.statusText}`;
        let errorData = {};

        try {
          // ูุญุงููุฉ ูุฑุงุกุฉ ุจูุงูุงุช ุงูุฎุทุฃ ูู JSON
          errorData = (await response.json()) as Record<string, any>;
          console.error(`โ API Error Response:`, errorData);
          if (typeof errorData === "object" && errorData !== null) {
            // ุงุณุชุฎุฏุงู ุงููุตูู ุฅูู ุงููุงุฆู ุจุทุฑููุฉ ุขููุฉ ูู ููุน ุงูุจูุงูุงุช
            const message =
              "message" in errorData ? String(errorData.message) : undefined;
            const error =
              "error" in errorData ? String(errorData.error) : undefined;
            errorMessage = message || error || errorMessage;
          }
        } catch (parseError) {
          // ุฅุฐุง ูู ุชูู ุงูุงุณุชุฌุงุจุฉ ุจุชูุณูู JSON
          console.error(`โ Failed to parse error response:`, parseError);
          try {
            // ูุญุงููุฉ ูุฑุงุกุฉ ุงูุงุณุชุฌุงุจุฉ ููุต
            const textResponse = await response.text();
            console.error(`โ Error Response Text:`, textResponse);
          } catch (textError) {
            console.error(
              `โ Failed to read error response as text:`,
              textError
            );
          }
        }

        return {
          success: false,
          error: errorMessage,
          statusCode: response.status,
        } as ApiResponse<T>;
      }

      // ูุญุงููุฉ ุชุญููู ุงูุงุณุชุฌุงุจุฉ ุงููุงุฌุญุฉ
      try {
        const data = await response.json();
        console.log(`โ API Response Success:`, {
          dataPreview: data ? typeof data : "null",
        });
        return { success: true, data };
      } catch (parseError) {
        console.error(`โ Failed to parse successful response:`, parseError);
        return {
          success: false,
          error: "Failed to parse API response",
          statusCode: 200,
        } as ApiResponse<T>;
      }
    } catch (error) {
      console.error(`โ API Request Failed:`, error);
      return {
        success: false,
        error:
          error instanceof Error ? error.message : "Network request failed",
        statusCode: 0, // ุนุฏู ูุฌูุฏ ุฑูุฒ ุญุงูุฉ ูุดูุฑ ุฅูู ูุดู ุงูุดุจูุฉ
      } as ApiResponse<T>;
    }
  }

